<template>
    <div>
        <mhead-top titleHead="立即支付" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-shopping-cart-1 icon" rgUrl="/cart/cart"></mhead-top>
        <div class="mBg">
            <div class="m-p-tips">请选择支付方式</div>
            <div class="m-p-choice">
                <div class="m-p-item bbGray" @click="changeSel('wxpay')">
                    <i :class="selVal=='wxpay'?'el-icon-success iconActive':'el-icon-success icon'"></i>
                    <div class="mp-pic">
                        <img src="../../images/wechat_logo.jpg">
                    </div>
                    <div class="mp-name">
                        微信支付
                    </div>
                </div>
                <div class="m-p-item" @click="changeSel('alipay')">
                    <i :class="selVal=='alipay'?'el-icon-success iconActive':'el-icon-success icon'"></i>
                    <div class="mp-pic">
                        <img src="../../images/alipay_logo.jpg">
                    </div>
                    <div class="mp-name">
                        支付宝
                    </div>
                </div>
            </div>
        </div>
        <div class="m-p-foot">
            <a :href="url" class="confirmBtn w tc" @click="confirm()">确定</a>
            
        </div>

        <!-- 微信支付弹框-->
        <el-dialog
                title="微信支付"
                :visible.sync="dialogVisible"
                width="60%"
                :before-close="handleClose">
            <div class="wxTit">
                应付金额：<span>￥{{orderDetail.price_total}}</span>
            </div>
            <!-- <div class="payPic wx tc">
                <el-image class="payImg" :src="wxPay.wxImage" fit="fill"></el-image>
            </div>
            <div class="tc mt10">使用微信扫描二维码进行支付</div> -->
           <span slot="footer" class="dialog-footer">
               <el-button @click="dialogVisible = false">取 消</el-button>
               <el-button type="primary">
                   <a v-if="isWeiXinBrowser"  class="wxSure wxBro"  @click="wxPayConfirm()">确定</a>
                   <a v-else :href="wxPay.wxUrl"  class="wxSure notwxB" @click="dialogVisible = false">确定</a>
               </el-button>
            </span>
        </el-dialog>


    </div>
</template>


<script>
    import mheadTop from '../../components/mobile/headTop';
    import {toAlipayOrder,getPayOrdersData,toWxpayOrder,toH5WxpayOrder,checkedWxOrderStatus,toJsapiWxpayOrder,getOpenIdToWx} from '../../service/api'

    export default {
        name: 'orderPay',
        data(){
            return{
                selVal:'wxpay',
                orderId:'',
                url:'javascript:void(0);',
                dialogVisible:false,//微信弹框
                wxPay:{
                    wxImage:'',
                    wxUrl:'',
                    orderNo:'',
                    money:''
                },
                timer: null,
                orderDetail:[],
                timeOut:0,//订单支付超时
                isWeiXinBrowser:false
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{

        },
        methods:{
            async initData(){
                //获取订单信息
                this.orderId = this.$route.query.orderId||'';
                var res = await getPayOrdersData(this.orderId);
                this.orderDetail = res;
                this.isWeiXinPlatform();
            },
            changeSel(val){
                this.selVal = val;
            },
            async confirm(){
                if(this.selVal == 'wxpay'){
                    this.dialogVisible = true;
                    if(this.isWeiXinBrowser == false){
                        var wx = await toH5WxpayOrder(this.orderId);
                        this.wxPay = wx;
                    }
                }else{
                    //选择支付宝支付
                    var res = await toAlipayOrder(this.orderId);
                    window.location.href=res.alipayUrl;
                }
            },
            onBridgeReady(params){
                var self = this;
                WeixinJSBridge.invoke(
                    'getBrandWCPayRequest', {
                        "appId":params.appId,     //公众号名称，由商户传入     
                        "timeStamp":params.timeStamp,         //时间戳，自1970年以来的秒数     
                        "nonceStr":params.nonceStr, //随机串     
                        "package":params.package,     
                        "signType":"MD5",         //微信签名方式：     
                        "paySign":params.paySign //微信签名 
                    },
                    function(res){
                        if (res.err_msg === "get_brand_wcpay_request:ok") {
                            console.log('支付成功！');
                            if(self.orderId==''){
                                self.$router.push({path:"/userCenter/order/order"});
                            }else{
                                self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                            }
                        } else if (res.err_msg === "get_brand_wcpay_request:fail") {
                            console.log('支付失败！');
                        }
                        // 使用以上方式判断前端返回,微信团队郑重提示：
                        //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                }); 
            },
            async wxPayConfirm(){
                console.log("this.isWeiXinBrowser");
                console.log(this.isWeiXinBrowser);
                //是微信浏览器，用JSAPI支付
                console.log("调用jsapi");
                var params = await toJsapiWxpayOrder(this.orderId);
                console.log("toJsapiWxpayOrder");
                console.log(params);
                if(params){
                    this.onBridgeReady(params);
                    this.wxPay = params;
                }
                this.dialogVisible = false;
            },
            async isWeiXinPlatform(){//判断是否微信平台
            　　var ua = window.navigator.userAgent.toLowerCase();
            　　if (ua.match(/MicroMessenger/i) == 'micromessenger'){
                    //是微信浏览器
                    var tar = await getOpenIdToWx(this.orderId);
                    console.log("getOpenIdToWx");
                    console.log(tar);
                    //window.location.href=tar.url;
                    console.log('这里是微信浏览器');
                    this.isWeiXinBrowser = true;
            　　} else {
                    //外部浏览器
                    console.log('其他浏览器');
                    this.isWeiXinBrowser = false;
            　　}
            },
            startLoop() {
                this.timer = setInterval(() => {
                    this.isPaySuccess()
                }, 3000);
            },
            async isPaySuccess() {
                var self = this;
                const orderId = self.orderId;
                var wxNumber = self.wxPay.orderNo ||'';
                if(wxNumber!=''){
                    const res = await checkedWxOrderStatus(wxNumber);
                    if (res.status === 12000) {
                        //支付成功
                        clearInterval(self.timer);
                        self.timer = null;
                        setTimeout(() => {
                            if(self.orderId==''){
                                self.$router.push({path:"/userCenter/order/order"});
                            }else{
                                self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                            }
                        }, 1500)
                    } else if(res.status === 18001){
                        //未知的错误发生
                        clearInterval(self.timer);
                        self.timer = null;
                        self.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                        setTimeout(() => {
                            if(self.orderId==''){
                                self.$router.push({path:"/userCenter/order/order"});
                            }else{
                                self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                            }
                        }, 1500)
                    }else{
                        //订单未支付
                        self.timeOut = self.timeOut +1;
                        if(self.timeOut == 20){
                            self.$message({
                                message: '订单支付已超时',
                                type: 'warning'
                            });
                            clearInterval(self.timer);
                            self.timer = null;
                            setTimeout(() => {
                                if(self.orderId==''){
                                    self.$router.push({path:"/userCenter/order/order"});
                                }else{
                                    self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                                }

                            },1000);
                        }
                    }
                }
            },
            handleClose(done) {
                this.$confirm('确认取消支付？')
                    .then(_ => {
                        done();
                        clearInterval(this.timer);
                        this.timer = null;
                        if(self.orderId==''){
                            self.$router.push({path:"/userCenter/order/order"});
                        }else{
                            self.$router.push({path:"/userCenter/order/detail",query:{order_id:this.orderId}});
                        }
                    })
                    .catch(_ => {});
            }

        },
        created(){
            this.startLoop();

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .m-p-tips{
        margin:px2rem(20);
    }
    .m-p-choice{
        background-color: #fff;
        margin-top:px2rem(20);
        padding:0 px2rem(20);
    }
    .m-p-item{
        display: flex;
        flex-wrap: nowrap;
        height: px2rem(104);
        line-height: px2rem(104);
    }
    .m-p-item .iconActive{
        font-size: px2rem(36);
        color:$default;
        line-height: px2rem(104);
        padding-right: px2rem(20);
    }
    .m-p-item .icon{
        font-size: px2rem(36);
        color:#ddd;
        line-height: px2rem(104);
        padding-right: px2rem(20);
    }
    .mp-pic{
        padding:px2rem(10) px2rem(20) 0 0;
    }
    .mp-pic img{
        width:px2rem(40);
        height: px2rem(40);
    }
    .wxTit{
        text-align: center;
        font-size: px2rem(28);
        color:#333;
        margin-bottom: 10px;
    }
    .wxTit span{
        font-size: px2rem(36);
        color:$colorprice;
    }

    /*foot Nav*/
    .m-p-foot{
        display: flex;
        flex-wrap:nowrap;
        height: px2rem(104);
        line-height: px2rem(104);
        background-color: #fff;
        position: fixed;
        left:0;
        bottom:0;
        width: px2rem(750);
    }
    .confirmBtn{
        background-color: $default;
        color:#fff;
        font-size: px2rem(32);
    }
    .confirmBtn:hover{
        color:#fff;
    }



</style>



